@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {

    font-size: 16px;
    font-family: "Open Sans", sans-serif;

    --primary-background: white;
    --secondary-background: #1013DE;
    --third-background: #191A69;

    --primary-text: #ffffff;
    --secondary-text: #12135E;
    --third-text: #000000;


    --transition-speed: 600ms;


}

body {
    color: black;

    background-color: white;

    margin: 0;
    padding: 0;
}

body::-webkit-scrollbar {
    width: 0.25rem;
}

body::-webkit-scrollbar-track {
    background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
    background: #6649b8;
}

main {
    margin-left: 5rem;
    padding: 1rem;
}

.navbar {
    box-sizing: border-box;
    z-index: 999999;

    width: 5rem;
    height: 100vh;
    position:fixed;
    background-color:  white;

    filter: brightness(80%);

    transition: 200ms ease;

}



.navbar-nav {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
}

.nav-item {
    width: 100%;
}

.nav-item:last-child {
    margin-top: auto;
}

.nav-link {
    position: relative;
    display: flex;
    align-items: center;
    height: 5rem;
    text-decoration: none;


    transition: var(--transition-speed);

    background-color: transparent;
    
}

.nav-item:hover .nav-link {
    color: white;
    font-size: 1.2rem;
}

.nav-item {
    position: relative;
}


.nav-item.active::before {

    content: "";
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background: white;
  filter: brightness(80%);
  transition: .5s;
  z-index: 2;

}

.nav-item.active:hover:before {
    left: 0;
    width: 100%;
  }

.nav-item:hover:before {
    left: 0;
    width: 100%;
  }


.nav-item::before {
    content: "";
  position: absolute;
  top: 0%;
  left: 0%;
  width: 0%;
  height: 100%;
  background: white;
  transition: .5s;
  z-index: 2;
  filter: brightness(50%);
}

svg {
    color: white;
    z-index: 100;
}




.nav-link .primary {

    

    color: black;
    transition: 200ms ease;


}




.nav-item.active:hover .primary, .nav-item.active:hover .nav-link {
    color: black;
}

.nav-item:hover .primary {

    color: white;

    font-size: 2.2rem;


}

.link-text {
    display: none;
    margin-left: 1rem;
    z-index: 999999;
}

.nav-link svg {
    font-size: 2rem;
    margin: 0 1.5rem;

}

.navbar:hover {
    width: 16rem;
}

.navbar:hover .link-text {
    display: block;
}





@media only screen and (max-width: 600px) {
    .navbar {
        bottom: 0;
        width: 100vw;
        height: 5rem;
    }

    .navbar:hover {
        width: 100vw;

    }

    .navbar:hover .link-text {
        display: none;
    }

    .navbar-nav {
        flex-direction: row;
    }   

    .nav-link {
        justify-content: center;
    }

    main {
        margin: 0;
    }


}